<template>
<div class="wrapper" style="cursor:auto">
    <!-- 头部 -->
    <editpagehead :content="content"></editpagehead>
    <!-- 标签页 -->
    <tagpage></tagpage>
    <!-- 主体 -->
    <div class="container" :style="{height:height-88+'px'}">
       <router-view></router-view>
    </div>
</div>
</template>
<script>
// 引入组件
import editpage_head from '@/components/EditPageComp/TagComponent/Header.vue'
import tagpage from '@/components/EditPageComp/TagComponent/Tagpage.vue'

export default {
    components:{
        editpagehead:editpage_head,
        tagpage
    },
    data :function() {
        return {
            //表单标题
            content:'调查问卷',
            //页面高度
            height: `${document.documentElement.clientHeight}`
        }
    },
    methods: {
        windowHeight:function (value) {
            this.$store.commit('windowheight/setheight',value)
        }
    },
    created() {
        this.windowHeight(document.documentElement.clientHeight);
    },
    mounted() {
        const _this = this;
        window.onresize = () => {
            return (() => {
                // 可以在这里保存到浏览器中，也可以保存到其他地方
                // window.height = document.documentElement.clientHeight;
                // _this.height = window.height;
                _this.height = `${document.documentElement.clientHeight}`;
            })();
        };
    },
    watch: {
        height (val) {
            if(!this.timer) {
                this.height = val
                this.timer = true
                let _this = this
                setTimeout(function () {
                    _this.timer = false
                }, 500)
            }
            // 这里可以添加修改时的方法
            this.windowHeight(val);
        }
    },
}
</script>
<style>
    .wrapper{
        height: 100%;
        text-align: center;
    }
    .container{
        padding: 0;
    }
</style>