<template>
  <el-row class="tabs">
    <el-col :span="12">
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane label="编辑" name="first"></el-tab-pane>
        <el-tab-pane label="概述" name="second"></el-tab-pane>
        <el-tab-pane label="数据" name="third"></el-tab-pane>
        <el-tab-pane label="统计图" name="fourth"></el-tab-pane>
      </el-tabs>
    </el-col>
    <el-col :span="4" :offset="8">
      <el-button type="primary" icon="el-icon-position" round class="publish-btn" size="mini">发布</el-button>
    </el-col>
  </el-row>
</template>
<script>
export default {
  data: function() {
    return {
      activeName: "first"
    };
  },
  methods: {
    handleClick() {
      let formId=this.$route.params.id
      if (this.activeName == "first") {
        sessionStorage.setItem('TagActiveName',this.activeName );
        this.$router.push({ path: "/formFill/maincontent/"+formId });
      } else if (this.activeName == "second") {
        sessionStorage.setItem('TagActiveName',this.activeName );
        this.$router.push({ path: "/formFill/summarypage/"+formId });
      } else if (this.activeName == "third") {
        sessionStorage.setItem('TagActiveName',this.activeName );
        this.$router.push({ path: "/formFill/formdata/"+formId });
      } else if (this.activeName == "fourth") {
        sessionStorage.setItem('TagActiveName',this.activeName );
        this.$router.push({ path: "/formFill/chartsdata/"+formId});
      }
    }
  },
  // created() {
  //   //请求表头数据
  //   this.handleClick();
  // }
  mounted:function(){
    this.activeName=sessionStorage.getItem('TagActiveName')
  }
};
</script>
<style scoped>
/* 标签页 */
.tabs >>> .el-tabs__nav-wrap,
.tabs >>> .el-tabs__nav,
.tabs >>> .el-tabs__item,
.tabs {
  background: #2a6bcd;
  height: 35px;
}
.tabs >>> .el-tabs--card > .el-tabs__header .el-tabs__item,
.tabs >>> .el-tabs--card > .el-tabs__header .el-tabs__nav {
  border: none;
}
.tabs >>> .el-tabs__item {
  /* border: 1px solid #2A6BCD ; */
  color: #a7abcd;
}
.tabs >>> .el-tabs__header {
  margin-bottom: 0;
}
.tabs >>> .el-tabs__item.is-active {
  background: white;
  color: #333333;
  border: 1px solid white;
  border-radius: 4px 4px 0 0;
}
.tabs >>> .el-tabs__nav {
  margin-left: 20px;
}
.tabs >>> .publish-btn {
  float: right;
  margin-right: 20px;
  size: 26px;
  color: black;
  background: #c0d3f0;
  border-color: #c0d3f0;
}
</style>